import React, { Fragment, useState } from "react";
import { AppBar, SlickSlider } from "components";
import { HubItem } from "./components";

const Hub = props => {
    const [loading] = useState(false);

    if (!!loading) {
        return <Loading />;
    }

    return (
        <Fragment>
            <AppBar />
            <main className="hub">
                <SlickSlider className="hub-slider">
                    <HubItem icon="portrait" title="Profile" />
                    <HubItem icon="school" title="Education" />
                    <HubItem icon="work" title="Experience" />
                    <HubItem icon="developer_mode" title="Abilities" />
                    <HubItem icon="contacts" title="Contact Me" />
                </SlickSlider>
            </main>
        </Fragment>
    );
};

export default Hub;
